body {
  min-width: 320px;
  margin: 0;
  font-family: 'Open Sans_my', sans-serif;
  color: #000;
}
.page-header {
  border-bottom: 1px solid #ebebeb;
}
.page-header__inner {
  display: block;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 17.5px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  align-items: center;
}
.site-logo {
  display: inline-block;
  width: 100%;
  height: 100%;
}
@media (min-width: 1199px) {
  .main-nav__list-mobile {
    display: none;
  }
}
@media (max-width: 1199px) {
  .main-nav {
    display: none;
  }
}
.main-nav__icon-mobile {
  max-width: 16;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  text-decoration: none;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding-bottom: 17.5px;
}
.main-nav--mobile {
  display: block;
  max-width: 100%;
  padding-left: 10px;
  padding-right: 10px;
}
.main-nav__list--mobile {
  margin: 0 -20px;
  text-align: center;
}
ul {
  padding-left: 0;
}
.main-nav__item--mobile {
  display: inline-block;
  width: 100%;
  text-align: center;
  padding: 14px 0;
  border-top: 1px solid #ebebeb;
}
.main-nav__icon-bar {
  display: inline-block;
  background-color: #ff7435;
  width: 10px;
  height: 2px;
  margin: 2.5px 0;
}
