.main-nav__icon-mobile {
    max-width: 16;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    text-decoration: none;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding-bottom: 17.5px;
}

.main-nav--mobile {
    display: block;
    max-width: 100%;
    padding-left: 10px;
    padding-right: 10px;
}

.main-nav__list--mobile {
    margin: 0 -20px;
    text-align: center;
}

ul {
    padding-left: 0;
}

.main-nav__item--mobile {
    display: inline-block;
    width: 100%;
    text-align: center;
    padding: 14px 0;
    border-top: 1px solid #ebebeb;
}

.main-nav__icon-bar {
display: inline-block;
background-color: #ff7435;
width: 10px;
height: 2px;
margin: 2.5px 0;
}
